@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Readex+Pro:wght@200;300;700&display=swap);
:root{font-size:16px;--main-light: #BEB7A4;--main-dark: #373F47}body,html,*,*::after,*::before{margin:0;padding:0;box-sizing:border-box}*{font-family:"Readex Pro","Roboto", sans-serif;font-weight:300}body h1{text-align:center;font-weight:700;background-color:#373F47;background-color:var(--main-dark);color:white;margin:0 0 1rem 0;padding:5% 0;box-shadow:rgba(50,50,93,0.25) 0px 6px 12px -2px,rgba(0,0,0,0.3) 0px 3px 7px -3px}body .buttons-wrapper{display:flex;justify-content:center;background-color:#373F47;background-color:var(--main-dark);color:#BEB7A4;color:var(--main-light);grid-gap:5%;gap:5%;margin-top:5%;padding:5% 0%}body .news-container{font-size:1rem;display:grid;grid-template-columns:1fr;grid-gap:1rem;gap:1rem}body .news-container .article{border:2px solid #373F47;border:2px solid var(--main-dark);border-radius:4px;display:flex;flex-direction:column;justify-content:center;align-items:center;margin:0 1rem;padding:.3rem;font-size:.9rem;line-height:150%;box-shadow:rgba(0,0,0,0.15) 2.4px 2.4px 3.2px}body .news-container .article img{max-width:100%;border-radius:.15rem;margin-bottom:.3rem}

